define('splunk/routes/hashtags', ['exports', 'ember', 'ember-infinity/mixins/route'], function (exports, Ember, InfinityRoute) {

  'use strict';

  var HashtagsRoute;

  HashtagsRoute = Ember['default'].Route.extend(InfinityRoute['default'], {
    model: function model() {
      return this.infinityModel('hashtag', {
        perPage: 50,
        startingPage: 1
      });
    }
  });

  exports['default'] = HashtagsRoute;

});