define('splunk/routes/tweets', ['exports', 'ember', 'ember-infinity/mixins/route'], function (exports, Ember, InfinityRoute) {

  'use strict';

  var TweetsRoute;

  TweetsRoute = Ember['default'].Route.extend(InfinityRoute['default'], {
    model: function model() {
      return this.infinityModel('tweet', {
        perPage: 50,
        startingPage: 1
      });
    }
  });

  exports['default'] = TweetsRoute;

});