define('splunk/routes/stream', ['exports', 'ember', 'ember-infinity/mixins/route'], function (exports, Ember, InfinityRoute) {

  'use strict';

  var StreamRoute;

  StreamRoute = Ember['default'].Route.extend(InfinityRoute['default'], {
    model: function model() {
      return [];
    },
    setupController: function setupController(controller, model) {
      return controller.set('model', model);
    }
  });

  exports['default'] = StreamRoute;

});