define('splunk/models/tweet', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var Tweet;

  Tweet = DS['default'].Model.extend({
    text: DS['default'].attr(),
    screen_name: DS['default'].attr(),
    created_at: DS['default'].attr(),
    favorite_count: DS['default'].attr,
    url: DS['default'].attr(),
    profile_image: DS['default'].attr(),
    media_url: DS['default'].attr(),
    hashtags: DS['default'].hasMany('hashtag')
  });

  exports['default'] = Tweet;

});