define('splunk/router', ['exports', 'ember', 'splunk/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router;

  Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.route('tweets');
    this.route('home');
    this.route('stream');
    this.route('hashtags');
    return this.route('about');
  });

  exports['default'] = Router;

});